import React from 'react';
import Link from 'gatsby-link';
import mobile from '../images/Siyu-mobile.png'

export default class Contact extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      email: '',
      message: ''
    }

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleEmptySubmit = this.handleEmptySubmit.bind(this);
  }
  handleChange(event) {
    this.setState({
      email: event.target.value,
    })
  }
  handleSubmit(event) {
    event.preventDefault();
    console.log(this.refs.email_address.value);
    fetch('http://199.180.133.127/api/recipients/', {
      method: 'post',
      headers: {
        'Accept': 'application/json',
        'content-type': 'application/json'
      },
      body: JSON.stringify({
        email: this.refs.email_address.value
      })
    })
      .then(data => console.log('data', data))
      .catch(error => console.log('error', error));

    this.setState({
      email: '',
      message: 'Thank you for signing up!'
    });
  }

  handleEmptySubmit(event) {
    event.preventDefault();
    this.setState({
      message: 'Please enter an email address'
    })
  }
  render() {
    const submitHandler = this.state.email ? this.handleSubmit : this.handleEmptySubmit;
    return (
      <div id="main">
        <section className="post">
          <header className="major">
            <h1>Weekly Newsletter</h1>
          </header>
          <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(200px, 1fr))' }}>
            <div >
              <p>
                Get our weekly brief in your email. Full of pertinent fincial news and topics. We'll send you one high quality email a week and never any spam.
              </p>
              <form id="email" onSubmit={submitHandler} style={{ padding: '3rem 3rem 0 3rem', minWidth: '50px' }} >
                <input value={this.state.email} onChange={this.handleChange} ref="email_address" size="50" className="fit" type="email" placeholder="Email Address" />
                <input id="submit" type="submit" />
              </form>
              {this.state.message && <span className='message'>{this.state.message}</span>}
            </div>
            <div style={{ justifySelf: 'center' }}>
              <img src={mobile} alt="" width="250" height="450" />
            </div>
          </div>
        </section>
      </div>
    );
  }
}



